import React, {Component } from "react";
import { Routes, Route, Link } from 'react-router-dom'
import "./games.css"

class GamesHeader extends Component {
    constructor(props) {
        super(props)
    }

    render() {
        return(
            <header>
                <Link to='/games/ssp'>Камень, ножницы, бумага</Link>
                <Link to='/games/sea-battle'>Морской бой</Link>
            </header>
        )
    }
}

export default GamesHeader;