import React, { Component } from "react"
import "./sorting.css"

class Sorting extends Component {
    constructor(props) {
        super(props)
        this.state = {
            array: "None",
            states: '',
            arrays: []
        }
        this.sendArrayToSort = this.sendArrayToSort.bind(this)
        this.sort_work = this.sort_work.bind(this)
    }

    handleSubmit = (event) => {
        event.preventDefault()
        this.sendArrayToSort(event.target[0].value)
    }

    async sendArrayToSort(array) {
        let obj = {
            method: 'POST',
            headers: {
              "Content-Type":"application/json"
            },
            body: JSON.stringify({
              'array': array,
            })
          }
      
          let response = await fetch("http://localhost:5000/sorting/", obj)
          let result = await response.json()

          this.setState({
              array: result.result,
              states: result.states
          })

          console.log(this.state.states)
    }

    sort_work() {
        if (this.state.states == '')
            return (<></>)

        let arrays = []
        for (let st in this.state.states) {
            console.log(this.state.states[st].array)
            arrays.push(this.state.states[st].array)
        }

        this.setState({
            arrays: arrays
        })

        return "ASD"
    }

    render() {
        return(
            <div>
                <div className='main'>
                    Сортировка массива
                </div>
                <form className='form' onSubmit={this.handleSubmit}>
                    <input type='text'
                            placeholder='Задайте массив'
                            className='input'
                    />
                    <button type='submit'
                            className='btn'
                    >
                        Отсортировать
                    </button>
                </form>
                {this.state.array}
                <div>
                    <button onClick={this.sort_work}>
                        Work of alghoritm
                    </button>
                </div>
                {this.state.arrays.map((array) => (
                    <div>
                        {JSON.stringify(array)}
                    </div>
                ))}
            </div>
        )
    }
}

export default Sorting;